<template>
    <OwnersCreateSubmenu />
    <div class="grid justify-items-center">
        <form class="w-120 bg-gray-100 rounded-2xl p-4">
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="ownerNameInput"
                    >Owner name</label
                >
                <input
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="ownerNameInput"
                    aria-describedby="ownerNameInputHelp"
                    v-model="owner.name"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="ownerDescInput"
                    >Description</label
                >
                <input
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="ownerDescInput"
                    aria-describedby="ownerDescInputHelp"
                    v-model="owner.description"
                />
            </div>

            <button
                @click="editClicked($event)"
                type="submit"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-green-200 hover:bg-green-400 hover:no-underline hover:text-black w-full"
            >
                Edit Owner
            </button>
        </form>
    </div>
</template>

<script lang="ts">
import BaseService from "@/services/base-service";
import store from "@/store";
import { IOwner } from "@/types/IOwner";
import OwnersCreateSubmenu from "@/views/owners/OwnersCreateSubmenu.vue";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {
        OwnersCreateSubmenu,
    },
    props: {
        ownerId: Function,
    },
})
export default class OwnersEdit extends Vue {
    private ownerService: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/Owners",
        store.state.token
    );

    private owner: IOwner = new IOwner();

    async created(): Promise<void> {
        store.dispatch("startLoading");

        const ownerResponse = await this.ownerService.get(
            this.$route.params.ownerId.toString()
        );

        if (ownerResponse.data) {
            this.owner = ownerResponse.data;
        }

        store.dispatch("endLoading");
    }

    async editClicked(event: Event): Promise<void> {
        event.preventDefault();

        store.dispatch("startLoading");

        const response = await this.ownerService.edit(
            this.owner,
            this.owner.id
        );

        if (response.statusCode === 204) {
            this.$router.push("/owners/index");
        }
        store.dispatch("endLoading");
    }
}
</script>
