
import { Options, Vue } from "vue-class-component";
import ApisCreateSubmenu from "@/views/apis/ApisCreateSubmenu.vue";
import { IApi } from "@/types/IApi";
import { IOwner } from "@/types/IOwner";
import store from "@/store";
import BaseService from "@/services/base-service";

@Options({
    components: { ApisCreateSubmenu },
})
export default class ApisCreate extends Vue {
    private service: BaseService<IApi> = new BaseService<IApi>(
        "https://publicapis.azurewebsites.net/api/v1/ApiBaseInformation",
        store.state.token
    );

    private ownerSerivce: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/Owners",
        store.state.token
    );

    api: IApi = new IApi();
    owners: IOwner[] = [];

    async created(): Promise<void> {
        store.dispatch("startLoading");

        const ownersResponse = await this.ownerSerivce.getAll();

        if (ownersResponse.data) {
            this.owners = ownersResponse.data;
        }

        store.dispatch("endLoading");
    }

    async addClicked(event: Event): Promise<void> {
        event.preventDefault();
        store.dispatch("startLoading");

        const response = await this.service.add(this.api);

        console.log(response);

        if (response.statusCode === 200) {
            this.$router.push("/apis/index");
        }

        store.dispatch("endLoading");
    }
}
