
import { Vue } from "vue-class-component";
import { ICategory } from "@/types/ICategory";
import BaseService from "@/services/base-service";
import store from "@/store";

export default class CategoriesIndex extends Vue {
    private categoryService: BaseService<ICategory> = new BaseService<ICategory>(
        "https://publicapis.azurewebsites.net/api/v1/Categories",
        store.state.token
    );

    private categories: ICategory[] = [];

    async created(): Promise<void> {
        store.dispatch("startLoading");
        this.getCategries();
        store.dispatch("endLoading");
    }

    async getCategries(): Promise<void> {
        const response = await this.categoryService.getAll();

        if (response.data) {
            this.categories = response.data;
        }
    }
}
