import axios from "axios";
import IBaseService from "../interfaces/IBaseService";
import { IFetchRespnse } from "../types/IFetchResponse";

export default class BaseService<TEntity> implements IBaseService<TEntity> {
    constructor(
        protected apiEndpointUrl: string, private jwt: string | null) {
    }

    private authHeaders = this.jwt !== undefined
        ? {
            Authorization: `Bearer ${this.jwt}`,
            "Content-Type": "application/json"
        }
        : {
            Authorization: ""
        };

    async getAll(): Promise<IFetchRespnse<TEntity[]>> {
        const url = this.apiEndpointUrl;
        try {
            const response = await axios
                .get(url, {
                    headers: this.authHeaders
                });
            if (response.data) {
                const data = (await response.data) as TEntity[];
                return {
                    statusCode: response.status,
                    data: data,
                }
            }
            return {
                statusCode: response.status,
                errorMessage: response.statusText
            }
        } catch (reason) {
            return {
                statusCode: 0,
                errorMessage: JSON.stringify(reason),
            }
        }
    }

    async get(id: string): Promise<IFetchRespnse<TEntity>> {
        let url = this.apiEndpointUrl;
        url = `${url}/${id}`;
        try {
            const response = await axios
                .get(url, {
                    headers: this.authHeaders
                });
            if (response.data) {
                const data = (await response.data) as TEntity;
                return {
                    statusCode: response.status,
                    data: data,
                }
            }
            return {
                statusCode: response.status,
                errorMessage: response.statusText
            }
        } catch (reason) {
            return {
                statusCode: 0,
                errorMessage: JSON.stringify(reason),
            }
        }
    }

    async add(entity: TEntity): Promise<IFetchRespnse<TEntity>> {
        const url = this.apiEndpointUrl;
        try {
            const response = await axios
                .post(url, JSON.stringify(entity), {
                    headers: this.authHeaders,
                });
            if (response.data) {
                const data = response.data as TEntity;
                return {
                    statusCode: 200,
                    data: data,
                }
            }
            return {
                statusCode: response.status,
                errorMessage: response.statusText
            }
        } catch (reason) {
            return {
                statusCode: 0,
                errorMessage: JSON.stringify(reason),
            }
        }
    }

    async edit(entity: TEntity, id: string): Promise<IFetchRespnse<TEntity>> {
        let url = this.apiEndpointUrl;
        url = `${url}/${id}`;
        try {
            const response = await axios
                .put(url, JSON.stringify(entity), {
                    headers: this.authHeaders,
                });
            if (response.data) {
                return {
                    statusCode: response.status,
                }
            }
            return {
                statusCode: response.status,
                errorMessage: response.statusText
            }
        } catch (reason) {
            return {
                statusCode: 0,
                errorMessage: JSON.stringify(reason),
            }
        }
    }

    async remove(id: string): Promise<IFetchRespnse<TEntity>> {
        let url = this.apiEndpointUrl;
        url = `${url}/${id}`;
        try {
            const response = await axios
                .delete(url, {
                    headers: this.authHeaders,
                });
            if (response.data) {
                return {
                    statusCode: response.status,
                }
            }
            return {
                statusCode: response.status,
                errorMessage: response.statusText
            }
        } catch (reason) {
            return {
                statusCode: 0,
                errorMessage: JSON.stringify(reason),
            }
        }
    }
}
