<template>
    <div class="bg-gray-100 border-gray-700 border-2 rounded-xl w-120">
        <router-link
            v-if="loggedIn"
            class="float-left hover:bg-yellow-400 rounded-tl-lg p-2 rounded-br-xl text-white"
            :to="{
                name: 'ApisEdit',
                params: { apiId: api.id },
            }"
        >
            🖊
        </router-link>

        <button
            v-on:click="remove(api.id)"
            v-if="loggedIn && !hasEndpoints(api)"
            class="float-right hover:bg-red-400 rounded-tr-lg p-2 rounded-bl-xl"
        >
            ✖
        </button>
        <div class="p-4 max-h-full">
            <div class="text-center pb-4">
                <span class="text-2xl font-semibold block"
                    >{{ api.name }}
                    <span class="text-xs text-gray-500 font-normal italic"
                        >by: {{ api.owner }}</span
                    ></span
                >
                <span class="text-sm text-gray-600 block">{{
                    api.baseUrl
                }}</span>
            </div>

            <div class="text-left italic p-2">
                {{ api.description }}
            </div>

            <hr />

            <div class="p-2">
                <router-link
                    v-if="loggedIn"
                    class="py-1 px-2 font-semibold rounded-lg shadow-md text-black bg-green-500 hover:bg-green-700 hover:no-underline hover:text-white w-full"
                    :to="{
                        name: 'AddEndpoint',
                        params: { apiId: api.id },
                    }"
                >
                    Add new endpoint
                </router-link>
                <div
                    class="flex py-2"
                    v-for="apiEndpoint in api.apiEndpoints"
                    :key="apiEndpoint.id"
                >
                    <input
                        class="w-full bg-gray-200 border-gray-300 border-2 text-sm px-2"
                        disabled
                        :value="api.baseUrl + apiEndpoint.endpointUrl"
                    />
                    <button
                        v-if="loggedIn"
                        v-on:click="removeEndpoint(apiEndpoint.id, api.id)"
                        class="float-right p-1 hover:bg-red-300"
                    >
                        ❌
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import store from "@/store";
import { IApi } from "@/types/IApi";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {},
    props: {
        api: Object,
    },
    emits: ["remove", "removeEndpoint"],
    methods: {
        remove(id: string) {
            this.$emit("remove", id);
        },
        removeEndpoint(id: string, apiId: string) {
            this.$emit("removeEndpoint", id, apiId);
        },
    },
})
export default class ApisIndexCard extends Vue {
    public get loggedIn(): boolean {
        return store.state.token !== null;
    }

    hasEndpoints(api: IApi): boolean {
        if (api.apiEndpoints) {
            return api.apiEndpoints.length > 0;
        }
        return false;
    }
}
</script>
