<template>
    <div class="bg-gray-100 border-gray-700 border-2 rounded-xl w-120">
        <router-link
            v-if="loggedIn"
            class="float-left hover:bg-yellow-400 rounded-tl-lg p-2 rounded-br-xl text-white"
            :to="{
                name: 'OwnersEdit',
                params: { ownerId: owner.id },
            }"
        >
            🖊
        </router-link>

        <button
            v-on:click="remove(owner.id)"
            v-if="loggedIn && !hasApis(owner)"
            class="float-right hover:bg-red-400 rounded-tr-lg p-2 rounded-bl-xl"
        >
            ✖
        </button>
        <div class="p-4 max-h-full">
            <div class="text-center text-2xl font-semibold pb-4">
                {{ owner.name }}
            </div>

            <div class="text-left italic p-2">
                {{ owner.description }}
            </div>

            <hr />

            <div class="p-2">
                <div class="flex" v-for="api in owner.apis" :key="api.id">
                    <input class="w-full" disabled :value="api.baseUrl" />
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import store from "@/store";
import { IOwner } from "@/types/IOwner";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {},
    props: {
        owner: Object,
    },
    emits: ["remove"],
    methods: {
        remove(id: string) {
            this.$emit("remove", id);
        },
    },
})
export default class OwnersIndexCard extends Vue {
    public get loggedIn(): boolean {
        return store.state.token !== null;
    }

    hasApis(owner: IOwner): boolean {
        if (owner.apis) {
            return owner.apis.length > 0;
        }
        return false;
    }
}
</script>
