<template>
    <OwnersCreateSubmenu />
    <div class="grid justify-items-center">
        <form class="w-120 bg-gray-100 rounded-2xl p-4">
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="ownerNameInput"
                    >Owner name</label
                >
                <input
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="ownerNameInput"
                    aria-describedby="ownerNameInput"
                    v-model="owner.name"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="ownerDescInput"
                    >Owner description</label
                >
                <input
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="ownerDescInput"
                    aria-describedby="ownerDescInputHelp"
                    v-model="owner.description"
                />
            </div>

            <button
                @click="addClicked($event)"
                type="submit"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-green-200 hover:bg-green-400 hover:no-underline hover:text-black w-full"
            >
                Add new Owner
            </button>
        </form>
    </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import OwnersCreateSubmenu from "@/views/owners/OwnersCreateSubmenu.vue";
import { IOwner } from "@/types/IOwner";
import store from "@/store";
import BaseService from "@/services/base-service";

@Options({
    components: { OwnersCreateSubmenu },
})
export default class OwnersCreate extends Vue {
    private ownerSerivce: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/Owners",
        store.state.token
    );

    owner: IOwner = new IOwner();

    async addClicked(event: Event): Promise<void> {
        event.preventDefault();
        store.dispatch("startLoading");

        const response = await this.ownerSerivce.add(this.owner);

        console.log(response);

        if (response.statusCode === 200) {
            this.$router.push("/owners/index");
        }

        store.dispatch("endLoading");
    }
}
</script>
