<template>
    <div class="grid justify-items-center">
        <form class="sm:w-full md:w-120 bg-yellow-500 p-4 rounded-2xl">
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="nameInput">Name</label>
                <input
                    type="text"
                    class="p-1"
                    id="nameInput"
                    aria-describedby="nameInputHelp"
                    v-model="fullName"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="emailInput"
                    >Email address</label
                >
                <input
                    autocomplete="email"
                    type="email"
                    class="p-1"
                    id="emailInput"
                    aria-describedby="emailHelp"
                    v-model="email"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="passwordInput"
                    >Password</label
                >
                <input
                    autocomplete="new-password"
                    type="password"
                    class="p-1"
                    id="passwordInput"
                    v-model="password"
                />
            </div>
            <button
                @click="registerClicked($event)"
                type="submit"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-white hover:bg-gray-200 hover:no-underline hover:text-black w-full"
            >
                <span class="float-left">👌</span>
                Register
                <span class="float-right">👌</span>
            </button>
        </form>
    </div>
</template>

<script lang="ts">
import store from "@/store/index";
import { Vue } from "vue-class-component";
export default class Register extends Vue {
    email: string = "";
    password: string = "";
    fullName: string = "";

    created(): void {
        if (store.state.token !== null) {
            this.$router.push("/");
        }
    }

    async registerClicked(event: Event): Promise<void> {
        event.preventDefault();
        store.dispatch("startLoading");

        const registerResult = await store.dispatch("register", {
            email: this.email,
            password: this.password,
            fullName: this.fullName,
        });

        if (registerResult === 200) {
            store.dispatch("endLoading");

            this.$router.push("/");
        }
        store.dispatch("endLoading");
    }
}
</script>
