
import BaseService from "@/services/base-service";
import store from "@/store";
import { IOwner } from "@/types/IOwner";
import OwnersCreateSubmenu from "@/views/owners/OwnersCreateSubmenu.vue";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {
        OwnersCreateSubmenu,
    },
    props: {
        ownerId: Function,
    },
})
export default class OwnersEdit extends Vue {
    private ownerService: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/Owners",
        store.state.token
    );

    private owner: IOwner = new IOwner();

    async created(): Promise<void> {
        store.dispatch("startLoading");

        const ownerResponse = await this.ownerService.get(
            this.$route.params.ownerId.toString()
        );

        if (ownerResponse.data) {
            this.owner = ownerResponse.data;
        }

        store.dispatch("endLoading");
    }

    async editClicked(event: Event): Promise<void> {
        event.preventDefault();

        store.dispatch("startLoading");

        const response = await this.ownerService.edit(
            this.owner,
            this.owner.id
        );

        if (response.statusCode === 204) {
            this.$router.push("/owners/index");
        }
        store.dispatch("endLoading");
    }
}
