import { IRegisterInfo } from "./../types/IRegister";
import { createStore } from "vuex";
import axios from "axios";
import { ILoginInfo } from '@/types/ILoginInfo';
import { IJwtResponse } from '@/types/IJwtResponse';
import { IState } from '@/types/IState';

export const initialState: IState = {
    token: null,
    fullName: '',
    loading: false,
};

export default createStore({
    state: initialState,

    mutations: {
        logOut: (state: IState) => {
            state.token = null;
            state.fullName = '';
        },
        logIn: (state: IState, loginInfo: IJwtResponse) => {
            state.token = loginInfo.token;
            state.fullName = loginInfo.fullName;
        },
        startLoading: (state: IState): void => {
            console.log("Started loading!");

            state.loading = true;
        },
        endLoading: (state: IState): void => {
            console.log("Ended loading!");

            state.loading = false;
        }
    },

    actions: {
        logIn: async (context, login: ILoginInfo): Promise<number> => {
            context.commit('startLoading');

            try {
                const response = await axios.post(
                    'https://publicapis.azurewebsites.net/api/v1/Account/Login',
                    login,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                );
                if (response.status === 200) {
                    const result = response.data as IJwtResponse;

                    context.commit('logIn', result);
                    context.commit('endLoading');
                    return response.status;
                }
            } catch (reason) {
                context.commit('endLoading');
                return 400;
            }
            context.commit('endLoading');
            return 0;
        },

        register: async function (context, register: IRegisterInfo) {
            try {
                const response = await axios.post(
                    'https://publicapis.azurewebsites.net/api/v1/Account/Register',
                    register,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                );
                if (response.status === 200) {
                    const result = response.data as IJwtResponse;

                    context.commit('logIn', result);
                    context.commit('endLoading');
                    return response.status;
                }
            } catch (reason) {
                context.commit('endLoading');
                return 400;
            }
            context.commit('endLoading');
            return 0;
        },

        startLoading: (context) => {
            context.commit('startLoading');
        },

        endLoading: (context) => {
            context.commit('endLoading');
        }
    },

    getters: {
    },

    modules: {
    },
})
