<template>
    <div class="grid justify-items-center">
        <form class="w-120 bg-gray-100 rounded-2xl p-4">
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="baseURLInput"
                    >Api Base URL</label
                >
                <input
                    autocomplete="url"
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="baseURLInput"
                    aria-describedby="baseURLInputHelp"
                    v-model="endpointUrl"
                />
            </div>

            <button
                @click="addClicked($event)"
                type="submit"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-green-200 hover:bg-green-400 hover:no-underline hover:text-black w-full"
            >
                Add new API Base URL
            </button>
        </form>
    </div>
</template>

<script lang="ts">
import BaseService from "@/services/base-service";
import store from "@/store";
import { IApiEndpoint } from "@/types/IApiEndpoint";
import { Vue } from "vue-class-component";

export default class EndpointCreate extends Vue {
    private service: BaseService<IApiEndpoint> = new BaseService<IApiEndpoint>(
        "https://publicapis.azurewebsites.net/api/v1/ApiEndpoints",
        store.state.token
    );

    private apiEndpoint: IApiEndpoint = new IApiEndpoint();
    private endpointUrl: string = "";

    async addClicked(event: Event): Promise<void> {
        event.preventDefault();

        this.apiEndpoint.endpointUrl = this.endpointUrl;
        this.apiEndpoint.apiBaseInformationId = this.$route.params.apiId.toString();

        const result = await this.service.add(this.apiEndpoint);

        if (result.statusCode === 200) {
            this.$router.push("/apis/index");
        }
    }
}
</script>
