<template>
    <div
        v-if="loading"
        class="fixed w-full h-full bg-gray-900 bg-opacity-90 top-0 left-0 right-0 bottom-0 z-10"
    ></div>
    <div class="mb-4">
        <div v-if="loading" class="py-1 bg-green-400 animate-pulse"></div>
        <div v-else class="py-1 bg-white animate-pulse"></div>
    </div>
</template>

<script lang="ts">
import store from "@/store/index";
import { Vue } from "vue-class-component";
export default class Loader extends Vue {
    get loading(): boolean {
        return store.state.loading;
    }
}
</script>
