<template>
    <ApisCreateSubmenu />
    <div class="grid justify-items-center">
        <form class="w-120 bg-gray-100 rounded-2xl p-4">
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="baseURLInput"
                    >Api Base URL</label
                >
                <input
                    autocomplete="url"
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="baseURLInput"
                    aria-describedby="baseURLInputHelp"
                    v-model="api.baseUrl"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="apiNameInput"
                    >Name</label
                >
                <input
                    autocomplete="url"
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="apiNameInput"
                    aria-describedby="baseURLInputHelp"
                    v-model="api.name"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="baseURLInput"
                    >Api description</label
                >
                <input
                    type="text"
                    class="p-1 border-2 border-gray-400 rounded-md"
                    id="baseURLInput"
                    aria-describedby="baseURLInputHelp"
                    v-model="api.description"
                />
            </div>
            <div class="flex flex-col pb-4">
                <label class="font-bold text-black" for="ownerSelect"
                    >Owner</label
                >
                <div class="p-1 border-2 border-gray-400 rounded-md">
                    <select
                        v-model="api.ownerId"
                        class="w-full h-full p-1"
                        id="ownerSelect"
                    >
                        <option
                            v-for="owner of owners"
                            :key="owner.id"
                            :value="owner.id"
                        >
                            {{ owner.name }}
                        </option>
                    </select>
                </div>
            </div>

            <button
                @click="editClicked($event)"
                type="submit"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-green-200 hover:bg-green-400 hover:no-underline hover:text-black w-full"
            >
                Edit Api
            </button>
        </form>
    </div>
</template>

<script lang="ts">
import BaseService from "@/services/base-service";
import store from "@/store";
import { IApi } from "@/types/IApi";
import { IOwner } from "@/types/IOwner";
import ApisCreateSubmenu from "@/views/apis/ApisCreateSubmenu.vue";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {
        ApisCreateSubmenu,
    },
    props: {
        apiId: Function,
    },
})
export default class ApisEdit extends Vue {
    private apiService: BaseService<IApi> = new BaseService<IApi>(
        "https://publicapis.azurewebsites.net/api/v1/ApiBaseInformation",
        store.state.token
    );

    private ownerSerivce: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/OwnerS",
        store.state.token
    );

    private api: IApi = new IApi();
    owners: IOwner[] = [];

    async created(): Promise<void> {
        store.dispatch("startLoading");

        const ownersResponse = await this.ownerSerivce.getAll();

        if (ownersResponse.data) {
            this.owners = ownersResponse.data;
        }

        const apiResponse = await this.apiService.get(
            this.$route.params.apiId.toString()
        );

        if (apiResponse.data) {
            this.api = apiResponse.data;
        }

        store.dispatch("endLoading");
    }

    async editClicked(event: Event): Promise<void> {
        event.preventDefault();

        store.dispatch("startLoading");

        const response = await this.apiService.edit(this.api, this.api.id);

        if (response.statusCode === 204) {
            this.$router.push("/apis/index");
        }
        store.dispatch("endLoading");
    }
}
</script>
