
import { Options, Vue } from "vue-class-component";
import { IApi } from "@/types/IApi";
import BaseService from "@/services/base-service";
import store from "@/store/index";
import ApisIndexCard from "@/views/apis/ApisIndexCard.vue";
import ApisIndexSubmenu from "@/views/apis/ApisIndexSubmenu.vue";
import { IApiEndpoint } from "@/types/IApiEndpoint";

@Options({
    components: {
        ApisIndexCard,
        ApisIndexSubmenu,
    },
})
export default class ApisIndex extends Vue {
    private service: BaseService<IApi> = new BaseService<IApi>(
        "https://publicapis.azurewebsites.net/api/v1/ApiBaseInformation",
        store.state.token
    );

    private endpointService: BaseService<IApiEndpoint> = new BaseService<IApiEndpoint>(
        "https://publicapis.azurewebsites.net/api/v1/ApiEndpoints",
        store.state.token
    );

    private apis: IApi[] = [];

    public get loggedIn(): boolean {
        return store.state.token !== null;
    }

    async created(): Promise<void> {
        store.dispatch("startLoading");
        const response = await this.service.getAll();

        if (response.data) {
            this.apis = response.data;
        }
        store.dispatch("endLoading");
    }

    async remove(id: string): Promise<void> {
        store.dispatch("startLoading");

        const response = await this.service.remove(id);

        if (response.statusCode === 204) {
            this.apis.splice(
                this.apis.findIndex((a) => a.id === id),
                1
            );
        }
        store.dispatch("endLoading");
    }

    async removeEndpoint(id: string, apiId: string): Promise<void> {
        store.dispatch("startLoading");

        const response = await this.endpointService.remove(id);

        if (response.statusCode === 204) {
            this.apis.forEach((api): void => {
                if (api.id === apiId) {
                    // eslint-disable-next-line no-unused-expressions
                    api.apiEndpoints?.forEach((endpoint) => {
                        if (endpoint.id === id) {
                            return api.apiEndpoints?.splice(
                                api.apiEndpoints.findIndex((e) => e.id === id),
                                1
                            );
                        }
                    });
                }
            });
        }
        store.dispatch("endLoading");
    }
}
