
import store from "@/store";
import { IOwner } from "@/types/IOwner";
import { Options, Vue } from "vue-class-component";

@Options({
    components: {},
    props: {
        owner: Object,
    },
    emits: ["remove"],
    methods: {
        remove(id: string) {
            this.$emit("remove", id);
        },
    },
})
export default class OwnersIndexCard extends Vue {
    public get loggedIn(): boolean {
        return store.state.token !== null;
    }

    hasApis(owner: IOwner): boolean {
        if (owner.apis) {
            return owner.apis.length > 0;
        }
        return false;
    }
}
