<template>
    <OwnersIndexSubmenu v-if="loggedIn" />
    <div class="flex flex-wrap justify-center">
        <OwnersIndexCard
            v-for="owner in owners"
            :key="owner.id"
            v-bind:owner="owner"
            @remove="remove"
        />
    </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import { IOwner } from "@/types/IOwner";
import BaseService from "@/services/base-service";
import store from "@/store";
import OwnersIndexCard from "@/views/owners/OwnersIndexCard.vue";
import OwnersIndexSubmenu from "@/views/owners/OwnersIndexSubmenu.vue";

@Options({
    components: {
        OwnersIndexCard,
        OwnersIndexSubmenu,
    },
})
export default class OwnersIndex extends Vue {
    private ownerService: BaseService<IOwner> = new BaseService<IOwner>(
        "https://publicapis.azurewebsites.net/api/v1/Owners",
        store.state.token
    );

    private owners: IOwner[] = [];

    async created(): Promise<void> {
        store.dispatch("startLoading");

        this.getOwners();

        store.dispatch("endLoading");
    }

    public get loggedIn(): boolean {
        return store.state.token !== null;
    }

    async getOwners(): Promise<void> {
        const response = await this.ownerService.getAll();

        if (response.data) {
            this.owners = response.data;
        }
    }

    async remove(id: string): Promise<void> {
        store.dispatch("startLoading");

        const response = await this.ownerService.remove(id);

        if (response.statusCode === 204) {
            this.owners.splice(
                this.owners.findIndex((a) => a.id === id),
                1
            );
        }
        store.dispatch("endLoading");
    }
}
</script>
