import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import Home from "@/views/Home.vue";
import Login from "@/views/identity/Login.vue";
import Register from "@/views/identity/Register.vue";
import ApisIndex from "@/views/apis/ApisIndex.vue";
import ApisCreate from "@/views/apis/ApisCreate.vue";
import ApisEdit from "@/views/apis/ApisEdit.vue";
import EndpointCreate from "@/views/endpoint/EndpointCreate.vue";
import CategoriesIndex from "@/views/categories/CategoriesIndex.vue";
import OwnersIndex from "@/views/owners/OwnersIndex.vue";
import OwnersCreate from "@/views/owners/OwnersCreate.vue";
import OwnersEdit from "@/views/owners/OwnersEdit.vue";

const routes: Array<RouteRecordRaw> = [
    {
        path: "/",
        name: "Home",
        component: Home
    },
    {
        path: "/identity/login",
        name: "Login",
        component: Login,
    },
    {
        path: "/identity/register",
        name: "Register",
        component: Register,
    },
    {
        path: "/apis/index",
        name: "ApisIndex",
        component: ApisIndex,
    },
    {
        path: "/apis/create",
        name: "ApisCreate",
        component: ApisCreate,
    },
    {
        path: "/apis/edit/:apiId",
        name: "ApisEdit",
        component: ApisEdit,
        props: {
            apiId: String,
        }
    },
    {
        path: "/apis/:apiId/endpoint/create",
        name: "AddEndpoint",
        component: EndpointCreate,
        props: {
            apiId: String,
        }
    },
    {
        path: "/categories/index",
        name: "CategoriesIndex",
        component: CategoriesIndex,
    },
    {
        path: "/owners/index",
        name: "OwnersIndex",
        component: OwnersIndex,
    },
    {
        path: "/owners/create",
        name: "OwnersCreate",
        component: OwnersCreate,
    },
    {
        path: "/owners/edit/:ownerId",
        name: "OwnersEdit",
        component: OwnersEdit,
        props: {
            ownerId: String,
        }
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
