<template>
    <nav class="flex bg-transparent text-white p-4">
        <div class="pr-2">
            <router-link class="navbar-brand text-2xl" to="/">🏴‍☠️</router-link>
        </div>
        <div class="justify-self-center self-center">
            <router-link
                tag="button"
                to="/apis/index"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-gray-100 hover:bg-gray-400 hover:no-underline hover:text-black mr-2"
            >
                All APIs
            </router-link>
            <router-link
                tag="button"
                to="/owners/index"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-gray-100 hover:bg-gray-400 hover:no-underline hover:text-black"
            >
                Owners
            </router-link>
        </div>
        <div class="self-center ml-auto">
            <router-link
                tag="button"
                v-if="token == null"
                to="/identity/login"
                class="mr-2 py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-green-500 hover:bg-green-700 hover:no-underline hover:text-white"
            >
                Login
            </router-link>
            <router-link
                tag="button"
                v-if="token == null"
                to="/identity/register"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-black bg-yellow-500 hover:bg-yellow-600 hover:no-underline hover:text-white"
            >
                Register
            </router-link>
            <a
                v-if="token != null"
                @click="logOut()"
                class="py-2 px-4 font-semibold rounded-lg shadow-md text-white bg-red-500 hover:bg-red-600 hover:no-underline hover:text-white cursor-pointer"
            >
                Log Out
            </a>
        </div>
    </nav>
</template>
<script lang="ts">
import store from "@/store/index";
import { Vue } from "vue-class-component";

export default class Header extends Vue {
    get token(): string | null {
        return store.state.token;
    }

    logOut(): void {
        store.commit("logOut");
        this.$router.push("/");
    }
}
</script>
